import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import styled, { createGlobalStyle } from 'styled-components';
import 'bootstrap/dist/css/bootstrap.min.css';
import { io } from 'socket.io-client';
import { FaShareAlt, FaFacebook, FaInstagram, FaWhatsapp, FaTelegram, FaEnvelope, FaCopy } from 'react-icons/fa';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import {
  FacebookShareButton,
  WhatsappShareButton,
  TelegramShareButton,
  EmailShareButton,
} from 'react-share';
import notificationSound from './notification-sound.mp3';
import watermark from './watermark.png';
import RadioPlayer from './RadioPlayer';
import Header from './Header';
import Footer from './Footer';

const GlobalStyle = createGlobalStyle`
  @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');
  body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: linear-gradient(-45deg, #b71c1c, #1e1e1e, #000000, #b71c1c);
    background-size: 400% 400%;
    animation: gradientBG 15s ease infinite;
    color: #ffffff; /* White text color */
  }

  @keyframes gradientBG {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
`;

const BackgroundContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 50px 20px 20px; /* Add top padding to offset the fixed header */
  overflow: hidden; /* Hide the scrollbar */
  position: relative; /* Added relative positioning */

  @media (max-width: 1052px) {
    padding: 50px 20px 20px; /* Increase top padding for smaller screens */
  }

  &::after {
    content: '';
    background: url(${watermark}) no-repeat center center;
    background-size: contain;
    opacity: 0.9; /* 50% opacity */
    position: absolute;
 
  align-items: center;
    top: -180px;
    
    width: 1000px;
    height: 1000px;
    z-index: -1; /* Ensure it's behind all other elements */
  }
`;

const TopContainer = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  margin-bottom: 20px;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px; /* Reduce bottom margin */
  }
`;

const AdContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px; /* Add margin to space ads vertically */

  @media (max-width: 768px) {
    margin-bottom: 10px; /* Reduce bottom margin for smaller screens */
  }
`;

const PlayerContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 20px; /* Add some margin between the ads and the player */

  @media (max-width: 768px) {
    margin: 10px 0; /* Reduce vertical margin for smaller screens */
  }
`;

const NewsContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 800px;
  margin: 20px 0;
`;

const MiddleAdContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 20px 0;

  @media (max-width: 768px) {
    margin: 10px 0;
  }
`;

const AdvertisementsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px 0;
`;

const Advertisement = styled.div`
  margin: 10px;
  img {
    width: 100%;
    height: auto;
    max-width: 300px;
    border-radius: 10px;
  }
`;

const NewsSectionContainer = styled.div`
  flex: 2;
  margin-right: 20px;
`;

const Heading = styled.h1`
  text-align: center;
  margin: 20px 0;
  color: #e0e0e0; /* Light grey color */
`;

const NewsItemCard = styled.div`
  margin-bottom: 15px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Darker shadow */
  transition: transform 0.3s;
  background-color: ${props => props.highlighted ? '#b71c1c' : '#333333'}; /* Dark grey background */
  color: #ffffff; /* White text color */

  &:hover {
    transform: translateY(-5px);
  }
`;

const ImgStyle = styled.img`
  width: 100%;
  height: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
`;

const VideoAvailableTextStyle = styled.span`
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #b71c1c; /* Red background */
  color: white;
  padding: 5px 10px;
  font-size: 12px;
  font-weight: bold;
  border-radius: 5px;
`;

const ModalStyle = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1050; /* Ensure modal is on top */
`;

const ModalContentStyle = styled.div`
  background: #1e1e1e; /* Dark grey background to match site */
  padding: 50px;
  border-radius: 10px;
  max-width: 80%;
  max-height: 80%;
  overflow: auto;
  color: #ffffff; /* White text color */
  position: relative; /* For positioning the close icon */
`;

const ImgModalStyle = styled.img`
  width: 100%;
  height: auto;
  max-height: 40vh;
  object-fit: contain;
  margin-bottom: 15px;
`;

const IframeStyle = styled.iframe`
  width: 100%;
  height: 400px;
  border: none;
  margin-bottom: 15px;
`;

const CloseButton = styled.button`
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 24px;
  color: #ffffff;
  cursor: pointer;

  &:hover {
    color: #ff0000; /* Optional: change color on hover */
  }
`;

const ShareButton = styled.button`
  position: absolute;
  top: 15px;
  right: 30px; /* Position to the left of the close button */
  background: transparent;
  border: none;
  font-size: 20px;
  color: #ffffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  z-index: 1060; /* Ensure share button is in front */

  &:hover {
    color: #ff0000; /* Optional: change color on hover */
  }

  svg {
    margin-right: 5px;
  }
`;

const ShowMoreButton = styled.button`
  display: block;
  margin: 20px auto;
  padding: 10px 20px;
  background-color: #b71c1c; /* Red background */
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;

  &:hover {
    background-color: #a00; /* Darker red */
  }
`;

const ShareModalStyle = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1100; /* Ensure share modal is on top */
`;

const ShareModalContentStyle = styled.div`
  background: #1e1e1e; /* Dark grey background to match site */
  padding: 20px;
  border-radius: 10px;
  max-width: 500px;
  width: 100%;
  color: #ffffff; /* White text color */
  position: relative;
`;

const ShareModalCloseButton = styled.button`
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 24px;
  color: #ffffff;
  cursor: pointer;

  &:hover {
    color: #ff0000; /* Optional: change color on hover */
  }
`;

const ShareLinkInput = styled.input`
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-top: 20px;
  background-color: #333333; /* Dark grey background */
  color: #ffffff; /* White text color */
`;

const ShareOptions = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
`;

const ShareButtonWrapper = styled.div`
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-top: 10px;

  button {
    background: transparent;
    border: none;
    color: #ffffff;
    font-size: 24px;
    cursor: pointer;

    &:hover {
      color: #ff0000; /* Optional: change color on hover */
    }
  }
`;

const Homepage = () => {
  const [ads, setAds] = useState([]);
  const [news, setNews] = useState([]);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);
  const [selectedPost, setSelectedPost] = useState(null);
  const [shareModalVisible, setShareModalVisible] = useState(false);
  const [copied, setCopied] = useState(false);
  const audioRef = useRef(null);
  const lastFetchedPostIdRef = useRef(null); // To keep track of the last fetched post ID

  useEffect(() => {
    const fetchAdvertisements = async () => {
      try {
        const result = await axios.get('/api/advertisements');
        setAds(result.data);
      } catch (error) {
        console.error('Error fetching advertisements:', error);
      }
    };

    fetchAdvertisements();

    const socket = io();

    socket.on('advertisements-updated', fetchAdvertisements);

    return () => {
      socket.off('advertisements-updated', fetchAdvertisements);
    };
  }, []);

  useEffect(() => {
    fetchNews(1); // Initial fetch

    const socket = io();

    socket.on('news-updated', fetchLatestNews);

    return () => {
      socket.off('news-updated', fetchLatestNews);
    };
  }, []);

  const fetchNews = async (page) => {
    try {
      const result = await axios.get(`/api/news?page=${page}&limit=15`);
      console.log('News data:', result.data);
      if (result.data.length > 0) {
        setNews(prevNews => page === 1 ? result.data : [...prevNews, ...result.data]);
        if (page === 1) {
          lastFetchedPostIdRef.current = result.data[0]?.id; // Initialize with the latest post ID
        }
      }
      if (result.data.length < 15) {
        setHasMore(false);
      }
    } catch (error) {
      console.error('Error fetching news:', error);
    }
  };

  const fetchLatestNews = async () => {
    try {
      const result = await axios.get(`/api/news?page=1&limit=1`); // Fetch the latest record
      console.log('Latest news data:', result.data);
      if (result.data.length > 0 && result.data[0].id !== lastFetchedPostIdRef.current) {
        setNews(prevNews => [result.data[0], ...prevNews]);
        playNotificationSound();
        lastFetchedPostIdRef.current = result.data[0].id; // Update with the new latest post ID
      }
    } catch (error) {
      console.error('Error fetching latest news:', error);
    }
  };

  const playNotificationSound = () => {
    if (audioRef.current) {
      audioRef.current.currentTime = 0; // Reset to start
      audioRef.current.play().catch((error) => {
        console.error('Error playing notification sound:', error);
      });
    }
  };

  const handlePostClick = (post) => {
    console.log('Selected post:', post);
    setSelectedPost(post);
  };

  const closeModal = () => {
    setSelectedPost(null);
  };

  const openShareModal = () => {
    setShareModalVisible(true);
  };

  const closeShareModal = () => {
    setShareModalVisible(false);
    setCopied(false); // Reset copied state
  };

  const getYoutubeEmbedUrl = (url) => {
    if (!url) return '';
    let videoId = '';
    if (url.includes('youtu.be')) {
      videoId = url.split('/').pop();
    } else if (url.includes('youtube.com')) {
      const params = new URLSearchParams(url.split('?')[1]);
      videoId = params.get('v');
    }
    const embedUrl = `https://www.youtube.com/embed/${videoId}`;
    console.log('Embed URL:', embedUrl);
    return embedUrl;
  };

  const handleShowMore = () => {
    const nextPage = page + 1;
    setPage(nextPage);
    fetchNews(nextPage);
  };

  const getShareableLink = () => {
    return selectedPost ? `${window.location.origin}/post/${selectedPost.id}` : '';
  };

  // Ensure we have at least four ads to display
  const [leftAd, rightAd, middleAd, fourthAd] = ads.slice(0, 4);

  return (
    <div>
      <Header />
      <BackgroundContainer>
        <GlobalStyle />

        <TopContainer>
          {leftAd && (
            <AdContainer>
              <Advertisement>
                <img src={leftAd.image_url} alt={`Advertisement ${leftAd.id}`} />
              </Advertisement>
            </AdContainer>
          )}
          {/* <PlayerContainer>
            <RadioPlayer />
          </PlayerContainer> */}
          {rightAd && (
            <AdContainer>
              <Advertisement>
                <img src={rightAd.image_url} alt={`Advertisement ${rightAd.id}`} />
              </Advertisement>
            </AdContainer>
          )}
        </TopContainer>

        <NewsContainer>
          <NewsSectionContainer className="container">
            <Heading>NEWS</Heading>
            <audio ref={audioRef} src={notificationSound} preload="auto">
              Your browser does not support the audio element.
            </audio>
            <div className="row">
              {news.map((post, index) => (
                <React.Fragment key={post.id}>
                  <div className="col-md-4">
                    <NewsItemCard className="card" onClick={() => handlePostClick(post)} highlighted={post.id === lastFetchedPostIdRef.current}>
                      <div style={{ position: 'relative' }}>
                        <ImgStyle src={post.image_url} alt={post.heading} className="card-img-top" />
                        {post.video_url && <VideoAvailableTextStyle>Video Available</VideoAvailableTextStyle>}
                      </div>
                      <div className="card-body">
                        <h5 className="card-title"><b>{post.heading}</b></h5>
                        <p className="card-text">{post.description.split(' ').slice(0, 15).join(' ')}...</p>
                        <p className="card-text"><small style={{ color: '#ffffff' }}>{new Date(post.posted_at).toLocaleString()}</small></p>
                      </div>
                    </NewsItemCard>
                  </div>
                  {index === 2 && middleAd && (
                    <div className="col-md-4">
                      <Advertisement>
                        <div><img src={middleAd.image_url} alt={`Advertisement ${middleAd.id}`} /></div>
                        <div><img src={fourthAd.image_url} alt={`Advertisement ${fourthAd.id}`} /></div>
                      </Advertisement>
                    </div>
                  )}
                  {index === 6 && fourthAd && (
                    <div className="col-md-4">
                      <Advertisement>
                        <div><img src={middleAd.image_url} alt={`Advertisement ${middleAd.id}`} /></div>
                        <div><img src={fourthAd.image_url} alt={`Advertisement ${fourthAd.id}`} /></div>
                      </Advertisement>
                    </div>
                  )}
                  {index === 5 && (
                    <div className="col-md-4">
                      <MiddleAdContainer>
                        <AdvertisementsContainer>
                          {ads.map(ad => (
                            <Advertisement key={ad.id}>
                              <img src={ad.image_url} alt={`Advertisement ${ad.id}`} />
                            </Advertisement>
                          ))}
                        </AdvertisementsContainer>
                      </MiddleAdContainer>
                    </div>
                  )}
                </React.Fragment>
              ))}
            </div>

            {hasMore && (
              <ShowMoreButton onClick={handleShowMore}>Show More</ShowMoreButton>
            )}

            {selectedPost && (
              <ModalStyle onClick={closeModal}>
                <ModalContentStyle onClick={(e) => e.stopPropagation()}>
                  <CloseButton onClick={closeModal}>&times;</CloseButton>
                  <ShareButton onClick={openShareModal}> Share
                    <FaShareAlt />
                  </ShareButton>
                  <h3><b>{selectedPost.heading}</b></h3>
                  <ImgModalStyle src={selectedPost.image_url} alt={selectedPost.heading} />
                  {selectedPost.video_url && (
                    <IframeStyle
                      src={getYoutubeEmbedUrl(selectedPost.video_url)}
                      allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                      allowFullScreen
                      title="Video"
                    />
                  )}
                  <p>{selectedPost.description}</p>
                  <p><small style={{ color: '#ffffff' }}>{new Date(selectedPost.posted_at).toLocaleString()}</small></p>
                </ModalContentStyle>
              </ModalStyle>
            )}

            {shareModalVisible && (
              <ShareModalStyle onClick={closeShareModal}>
                <ShareModalContentStyle onClick={(e) => e.stopPropagation()}>
                  <ShareModalCloseButton onClick={closeShareModal}>&times;</ShareModalCloseButton>
                  <h3>Share this post</h3>
                  <ShareLinkInput
                    type="text"
                    value={getShareableLink()}
                    readOnly
                    onClick={(e) => e.target.select()}
                  />
                  <ShareOptions>
                    <CopyToClipboard text={getShareableLink()} onCopy={() => setCopied(true)}>
                      <button>
                        <FaCopy /> {copied ? 'Copied' : 'Copy Link'}
                      </button>
                    </CopyToClipboard>
                    <ShareButtonWrapper>
                      <FacebookShareButton url={getShareableLink()}>
                        <FaFacebook />
                      </FacebookShareButton>
                      <WhatsappShareButton url={getShareableLink()}>
                        <FaWhatsapp />
                      </WhatsappShareButton>
                      <TelegramShareButton url={getShareableLink()}>
                        <FaTelegram />
                      </TelegramShareButton>
                      <EmailShareButton url={getShareableLink()} subject={selectedPost?.heading} body={selectedPost?.description}>
                        <FaEnvelope />
                      </EmailShareButton>
                    </ShareButtonWrapper>
                  </ShareOptions>
                </ShareModalContentStyle>
              </ShareModalStyle>
            )}
          </NewsSectionContainer>
        </NewsContainer>
      </BackgroundContainer>
      <Footer />
    </div>
  );
};

export default Homepage;
