// AdminDashboard.jsx
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import { io } from 'socket.io-client';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import HeaderAdmin from './HeaderAdmin';
import Footer from './Footer';
import { useNavigate } from 'react-router-dom';

const PageContainer = styled.div`
  background-color: #000;
  color: #fff;
  padding: 20px;
  min-height: 100vh;
  padding: 10px 20px 20px; /* Add top padding to offset the fixed header */
  overflow: hidden; /* Hide the scrollbar */
`;

const SectionContainer = styled.div`
  background-color: #1e1e1e;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 40px;
`;

const SectionHeading = styled.h3`
  color: #ff0000;
  margin-bottom: 20px;
  text-align: center;
`;

const UploadForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
`;

const FileInput = styled.input`
  margin: 10px 0;
`;

const TextInput = styled.input`
  margin: 10px 0;
  padding: 10px;
  width: 300px;
`;

const TextArea = styled.textarea`
  margin: 10px 0;
  padding: 10px;
  width: 300px;
  height: 100px;
`;

const SubmitButton = styled.button`
  padding: 10px 20px;
  background-color: #ff0000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #cc0000;
  }

  &:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
`;

const AdvertisementList = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0;
`;

const AdvertisementItem = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  margin-right: 10px;
`;

const AdvertisementImage = styled.img`
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-right: 10px;
`;

const AdvertisementButton = styled.button`
  margin-left: 10px;
  padding: 5px 10px;
  background-color: #ff0000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #cc0000;
  }
`;

const NewsList = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0;
`;

const NewsItem = styled.div`
  width: 200px;
  margin: 10px;
  text-align: center;
`;

const NewsImage = styled.img`
  width: 100%;
  height: auto;
  object-fit: cover;
`;

const NewsButton = styled.button`
  margin: 5px;
  padding: 5px 10px;
  background-color: #ff0000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #cc0000;
  }
`;

const AdminDashboard = () => {
  const navigate = useNavigate();
  const [newsFile, setNewsFile] = useState(null);
  const [newsHeading, setNewsHeading] = useState('');
  const [newsDescription, setNewsDescription] = useState('');
  const [newsVideoUrl, setNewsVideoUrl] = useState('');
  const [adFile, setAdFile] = useState(null);
  const [advertisements, setAdvertisements] = useState([]);
  const [news, setNews] = useState([]);

  const [newsFileEnglish, setNewsFileEnglish] = useState(null);
  const [newsHeadingEnglish, setNewsHeadingEnglish] = useState('');
  const [newsDescriptionEnglish, setNewsDescriptionEnglish] = useState('');
  const [newsVideoUrlEnglish, setNewsVideoUrlEnglish] = useState('');
  const [adFileEnglish, setAdFileEnglish] = useState(null);
  const [advertisementsEnglish, setAdvertisementsEnglish] = useState([]);
  const [newsEnglish, setNewsEnglish] = useState([]);

  const [newsFileTamil, setNewsFileTamil] = useState(null);
  const [newsHeadingTamil, setNewsHeadingTamil] = useState('');
  const [newsDescriptionTamil, setNewsDescriptionTamil] = useState('');
  const [newsVideoUrlTamil, setNewsVideoUrlTamil] = useState('');
  const [adFileTamil, setAdFileTamil] = useState(null);
  const [advertisementsTamil, setAdvertisementsTamil] = useState([]);
  const [newsTamil, setNewsTamil] = useState([]);

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      navigate('/login');
      return;
    }

    const socket = io('https://www.helloradio.lk', {
      auth: {
        token
      }
    });

    socket.on('advertisements-updated', fetchAdvertisements);
    socket.on('news-updated', fetchNews);
    socket.on('news-deleted', fetchNews);

    socket.on('english-advertisements-updated', fetchAdvertisementsEnglish);
    socket.on('english-news-updated', fetchNewsEnglish);
    socket.on('english-news-deleted', fetchNewsEnglish);

    socket.on('tamil-advertisements-updated', fetchAdvertisementsTamil);
    socket.on('tamil-news-updated', fetchNewsTamil);
    socket.on('tamil-news-deleted', fetchNewsTamil);

    fetchAdvertisements();
    fetchNews();

    fetchAdvertisementsEnglish();
    fetchNewsEnglish();

    fetchAdvertisementsTamil();
    fetchNewsTamil();

    return () => {
      socket.disconnect();
    };
  }, [navigate]);

  const fetchAdvertisements = async () => {
    try {
      const result = await axios.get('/api/advertisements');
      setAdvertisements(result.data);
    } catch (error) {
      console.error('Error fetching advertisements:', error);
    }
  };

  const fetchNews = async () => {
    try {
      const result = await axios.get('/api/news?page=1&limit=10');
      setNews(result.data);
    } catch (error) {
      console.error('Error fetching news:', error);
    }
  };

  const fetchAdvertisementsEnglish = async () => {
    try {
      const result = await axios.get('/api/english-advertisements');
      setAdvertisementsEnglish(result.data);
    } catch (error) {
      console.error('Error fetching advertisements:', error);
    }
  };

  const fetchNewsEnglish = async () => {
    try {
      const result = await axios.get('/api/english-news?page=1&limit=10');
      setNewsEnglish(result.data);
    } catch (error) {
      console.error('Error fetching news:', error);
    }
  };

  const fetchAdvertisementsTamil = async () => {
    try {
      const result = await axios.get('/api/tamil-advertisements');
      setAdvertisementsTamil(result.data);
    } catch (error) {
      console.error('Error fetching advertisements:', error);
    }
  };

  const fetchNewsTamil = async () => {
    try {
      const result = await axios.get('/api/tamil-news?page=1&limit=10');
      setNewsTamil(result.data);
    } catch (error) {
      console.error('Error fetching news:', error);
    }
  };

  const handleNewsFileChange = (e) => {
    setNewsFile(e.target.files[0]);
  };

  const handleAdFileChange = (e) => {
    setAdFile(e.target.files[0]);
  };

  const handleNewsSubmit = async (e) => {
    e.preventDefault();
    if (!newsHeading || !newsDescription || !newsFile) {
      toast.error("Heading, Description, and Image are required");
      return;
    }
    if (newsFile.size > 1048576) {
      toast.error("File size should be less than 1MB");
      return;
    }

    const formData = new FormData();
    formData.append('image', newsFile);
    formData.append('heading', newsHeading);
    formData.append('description', newsDescription);
    formData.append('video_url', newsVideoUrl);

    try {
      const result = await axios.post('/api/news', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      toast.success('News post uploaded');
      cleanFields();
      fetchNews(); // Refresh news list
    } catch (error) {
      console.error('Error uploading news post:', error);
    }
  };

  const handleAdSubmit = async (e) => {
    e.preventDefault();
    if (!adFile) {
      toast.error("Image is required");
      return;
    }
    if (adFile.size > 1048576) {
      toast.error("File size should be less than 1MB");
      return;
    }

    const formData = new FormData();
    formData.append('image', adFile);

    try {
      const result = await axios.post('/api/advertisements', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      toast.success('Advertisement uploaded');
      cleanFields();
      fetchAdvertisements(); // Refresh advertisements list
    } catch (error) {
      console.error('Error uploading advertisement:', error);
    }
  };

  const handleDeleteAd = async (id) => {
    try {
      await axios.delete(`/api/advertisements/${id}`);
      fetchAdvertisements();
    } catch (error) {
      console.error('Error deleting advertisement:', error);
    }
  };

  const handleDeleteNews = async (id) => {
    try {
      await axios.delete(`/api/news/${id}`);
      fetchNews();
    } catch (error) {
      console.error('Error deleting news post:', error);
    }
  };

  const handleEditNews = async (id) => {
    console.log(`Edit news post with id: ${id}`);
  };

  const handleNewsFileChangeEnglish = (e) => {
    setNewsFileEnglish(e.target.files[0]);
  };

  const handleAdFileChangeEnglish = (e) => {
    setAdFileEnglish(e.target.files[0]);
  };

  const handleNewsSubmitEnglish = async (e) => {
    e.preventDefault();
    if (!newsHeadingEnglish || !newsDescriptionEnglish || !newsFileEnglish) {
      toast.error("Heading, Description, and Image are required");
      return;
    }
    if (newsFileEnglish.size > 1048576) {
      toast.error("File size should be less than 1MB");
      return;
    }

    const formData = new FormData();
    formData.append('image', newsFileEnglish);
    formData.append('heading', newsHeadingEnglish);
    formData.append('description', newsDescriptionEnglish);
    formData.append('video_url', newsVideoUrlEnglish);

    try {
      const result = await axios.post('/api/english-news', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      toast.success('News post uploaded');
      cleanFields();
      fetchNewsEnglish(); // Refresh news list
    } catch (error) {
      console.error('Error uploading news post:', error);
    }
  };

  const handleAdSubmitEnglish = async (e) => {
    e.preventDefault();
    if (!adFileEnglish) {
      toast.error("Image is required");
      return;
    }
    if (adFileEnglish.size > 1048576) {
      toast.error("File size should be less than 1MB");
      return;
    }

    const formData = new FormData();
    formData.append('image', adFileEnglish);

    try {
      const result = await axios.post('/api/english-advertisements', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      toast.success('Advertisement uploaded');
      cleanFields();
      fetchAdvertisementsEnglish(); // Refresh advertisements list
    } catch (error) {
      console.error('Error uploading advertisement:', error);
    }
  };

  const handleDeleteAdEnglish = async (id) => {
    try {
      await axios.delete(`/api/english-advertisements/${id}`);
      fetchAdvertisementsEnglish();
    } catch (error) {
      console.error('Error deleting advertisement:', error);
    }
  };

  const handleDeleteNewsEnglish = async (id) => {
    try {
      await axios.delete(`/api/english-news/${id}`);
      fetchNewsEnglish();
    } catch (error) {
      console.error('Error deleting news post:', error);
    }
  };

  const handleEditNewsEnglish = async (id) => {
    console.log(`Edit news post with id: ${id}`);
  };

  const handleNewsFileChangeTamil = (e) => {
    setNewsFileTamil(e.target.files[0]);
  };

  const handleAdFileChangeTamil = (e) => {
    setAdFileTamil(e.target.files[0]);
  };

  const handleNewsSubmitTamil = async (e) => {
    e.preventDefault();
    if (!newsHeadingTamil || !newsDescriptionTamil || !newsFileTamil) {
      toast.error("Heading, Description, and Image are required");
      return;
    }
    if (newsFileTamil.size > 1048576) {
      toast.error("File size should be less than 1MB");
      return;
    }

    const formData = new FormData();
    formData.append('image', newsFileTamil);
    formData.append('heading', newsHeadingTamil);
    formData.append('description', newsDescriptionTamil);
    formData.append('video_url', newsVideoUrlTamil);

    try {
      const result = await axios.post('/api/tamil-news', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      toast.success('News post uploaded');
      cleanFields();
      fetchNewsTamil(); // Refresh news list
    } catch (error) {
      console.error('Error uploading news post:', error);
    }
  };

  const handleAdSubmitTamil = async (e) => {
    e.preventDefault();
    if (!adFileTamil) {
      toast.error("Image is required");
      return;
    }
    if (adFileTamil.size > 1048576) {
      toast.error("File size should be less than 1MB");
      return;
    }

    const formData = new FormData();
    formData.append('image', adFileTamil);

    try {
      const result = await axios.post('/api/tamil-advertisements', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      toast.success('Advertisement uploaded');
      cleanFields();
      fetchAdvertisementsTamil(); // Refresh advertisements list
    } catch (error) {
      console.error('Error uploading advertisement:', error);
    }
  };

  const handleDeleteAdTamil = async (id) => {
    try {
      await axios.delete(`/api/tamil-advertisements/${id}`);
      fetchAdvertisementsTamil();
    } catch (error) {
      console.error('Error deleting advertisement:', error);
    }
  };

  const handleDeleteNewsTamil = async (id) => {
    try {
      await axios.delete(`/api/tamil-news/${id}`);
      fetchNewsTamil();
    } catch (error) {
      console.error('Error deleting news post:', error);
    }
  };

  const handleEditNewsTamil = async (id) => {
    console.log(`Edit news post with id: ${id}`);
  };

  const cleanFields = () => {
    setNewsFile(null);
    setNewsHeading('');
    setNewsDescription('');
    setNewsVideoUrl('');
    setAdFile(null);
    setNewsFileEnglish(null);
    setNewsHeadingEnglish('');
    setNewsDescriptionEnglish('');
    setNewsVideoUrlEnglish('');
    setAdFileEnglish(null);
    setNewsFileTamil(null);
    setNewsHeadingTamil('');
    setNewsDescriptionTamil('');
    setNewsVideoUrlTamil('');
    setAdFileTamil(null);
  };

  return (
    <>
      <HeaderAdmin defaultMuted={true} />
      <PageContainer>
        <h2>Hello Radio Dashboard</h2>

        <SectionContainer>
          <SectionHeading>Sinhala News and Advertisements</SectionHeading>
          <UploadForm onSubmit={handleNewsSubmit}>
            <h3 className="text-white">Upload News Post</h3>
            <TextInput
              type="text"
              placeholder="Heading"
              value={newsHeading}
              onChange={(e) => setNewsHeading(e.target.value)}
            />
            <TextArea
              placeholder="Description"
              value={newsDescription}
              onChange={(e) => setNewsDescription(e.target.value)}
            />
            <TextInput
              type="text"
              placeholder="Video URL"
              value={newsVideoUrl}
              onChange={(e) => setNewsVideoUrl(e.target.value)}
            />
            <FileInput type="file" onChange={handleNewsFileChange} />
            <SubmitButton type="submit">Upload News</SubmitButton>
          </UploadForm>

          <UploadForm onSubmit={handleAdSubmit}>
            <h3 className="text-white">Upload Advertisement</h3>
            <FileInput type="file" onChange={handleAdFileChange} />
            <SubmitButton type="submit" disabled={advertisements.length >= 2}>Upload Advertisement</SubmitButton>
          </UploadForm>

          <h3 className="text-white">Current Advertisements</h3>
          <AdvertisementList>
            {advertisements.map(ad => (
              <AdvertisementItem key={ad.id}>
                <AdvertisementImage src={ad.image_url} alt="Advertisement" />
                <AdvertisementButton onClick={() => handleDeleteAd(ad.id)}>Delete</AdvertisementButton>
              </AdvertisementItem>
            ))}
          </AdvertisementList>

          <h3 className="text-white">Last 10 News Posts</h3>
          <NewsList>
            {news.map(post => (
              <NewsItem key={post.id}>
                <NewsImage src={post.image_url} alt="News" />
                <div>
                  <p className="text-white"><strong>{post.heading}</strong></p>
                  <p className="text-white">{post.description.split(' ').slice(0, 6).join(' ')}...</p>
                  <NewsButton onClick={() => handleEditNews(post.id)}>Edit</NewsButton>
                  <NewsButton onClick={() => handleDeleteNews(post.id)}>Delete</NewsButton>
                </div>
              </NewsItem>
            ))}
          </NewsList>
        </SectionContainer>

        <SectionContainer>
          <SectionHeading>English News and Advertisements</SectionHeading>
          <UploadForm onSubmit={handleNewsSubmitEnglish}>
            <h3 className="text-white">Upload News Post</h3>
            <TextInput
              type="text"
              placeholder="Heading"
              value={newsHeadingEnglish}
              onChange={(e) => setNewsHeadingEnglish(e.target.value)}
            />
            <TextArea
              placeholder="Description"
              value={newsDescriptionEnglish}
              onChange={(e) => setNewsDescriptionEnglish(e.target.value)}
            />
            <TextInput
              type="text"
              placeholder="Video URL"
              value={newsVideoUrlEnglish}
              onChange={(e) => setNewsVideoUrlEnglish(e.target.value)}
            />
            <FileInput type="file" onChange={handleNewsFileChangeEnglish} />
            <SubmitButton type="submit">Upload News</SubmitButton>
          </UploadForm>

          <UploadForm onSubmit={handleAdSubmitEnglish}>
            <h3 className="text-white">Upload Advertisement</h3>
            <FileInput type="file" onChange={handleAdFileChangeEnglish} />
            <SubmitButton type="submit" disabled={advertisementsEnglish.length >= 2}>Upload Advertisement</SubmitButton>
          </UploadForm>

          <h3 className="text-white">Current Advertisements</h3>
          <AdvertisementList>
            {advertisementsEnglish.map(ad => (
              <AdvertisementItem key={ad.id}>
                <AdvertisementImage src={ad.image_url} alt="Advertisement" />
                <AdvertisementButton onClick={() => handleDeleteAdEnglish(ad.id)}>Delete</AdvertisementButton>
              </AdvertisementItem>
            ))}
          </AdvertisementList>

          <h3 className="text-white">Last 10 News Posts</h3>
          <NewsList>
            {newsEnglish.map(post => (
              <NewsItem key={post.id}>
                <NewsImage src={post.image_url} alt="News" />
                <div>
                  <p className="text-white"><strong>{post.heading}</strong></p>
                  <p className="text-white">{post.description.split(' ').slice(0, 6).join(' ')}...</p>
                  <NewsButton onClick={() => handleEditNewsEnglish(post.id)}>Edit</NewsButton>
                  <NewsButton onClick={() => handleDeleteNewsEnglish(post.id)}>Delete</NewsButton>
                </div>
              </NewsItem>
            ))}
          </NewsList>
        </SectionContainer>

        <SectionContainer>
          <SectionHeading>Tamil News and Advertisements</SectionHeading>
          <UploadForm onSubmit={handleNewsSubmitTamil}>
            <h3 className="text-white">Upload News Post</h3>
            <TextInput
              type="text"
              placeholder="Heading"
              value={newsHeadingTamil}
              onChange={(e) => setNewsHeadingTamil(e.target.value)}
            />
            <TextArea
              placeholder="Description"
              value={newsDescriptionTamil}
              onChange={(e) => setNewsDescriptionTamil(e.target.value)}
            />
            <TextInput
              type="text"
              placeholder="Video URL"
              value={newsVideoUrlTamil}
              onChange={(e) => setNewsVideoUrlTamil(e.target.value)}
            />
            <FileInput type="file" onChange={handleNewsFileChangeTamil} />
            <SubmitButton type="submit">Upload News</SubmitButton>
          </UploadForm>

          <UploadForm onSubmit={handleAdSubmitTamil}>
            <h3 className="text-white">Upload Advertisement</h3>
            <FileInput type="file" onChange={handleAdFileChangeTamil} />
            <SubmitButton type="submit" disabled={advertisementsTamil.length >= 2}>Upload Advertisement</SubmitButton>
          </UploadForm>

          <h3 className="text-white">Current Advertisements</h3>
          <AdvertisementList>
            {advertisementsTamil.map(ad => (
              <AdvertisementItem key={ad.id}>
                <AdvertisementImage src={ad.image_url} alt="Advertisement" />
                <AdvertisementButton onClick={() => handleDeleteAdTamil(ad.id)}>Delete</AdvertisementButton>
              </AdvertisementItem>
            ))}
          </AdvertisementList>

          <h3 className="text-white">Last 10 News Posts</h3>
          <NewsList>
            {newsTamil.map(post => (
              <NewsItem key={post.id}>
                <NewsImage src={post.image_url} alt="News" />
                <div>
                  <p className="text-white"><strong>{post.heading}</strong></p>
                  <p className="text-white">{post.description.split(' ').slice(0, 6).join(' ')}...</p>
                  <NewsButton onClick={() => handleEditNewsTamil(post.id)}>Edit</NewsButton>
                  <NewsButton onClick={() => handleDeleteNewsTamil(post.id)}>Delete</NewsButton>
                </div>
              </NewsItem>
            ))}
          </NewsList>
        </SectionContainer>
      </PageContainer>
      <Footer />
      <ToastContainer />
    </>
  );
};

export default AdminDashboard;
